<template>

    <div id="ecart2">
            <b-container id="ecart">
                <b-row id="ecart2" align-h="center" class="font-weight-bold">
                    <h5>Nombre de gardes du mois :</h5>
                </b-row>
                <b-row id="ecart2" align-h="center" class="font-weight-bold">
                    <b-col ></b-col>
                    <b-col > 
                            <b-badge pill variant="success"><h5>{{this.gardeNombre()}}</h5></b-badge>
                    </b-col>
                    <b-col ></b-col>
                </b-row>
                <hr>
                <b-row id="ecart2" align-h="center">
                    <b-col></b-col>
                    <b-col col>
                        <div>
                            <h5 class="text-left">Nouvelle garde</h5>
                            <span>Du: </span>
                            <label><b-input type="date" v-model="gardeNouvelle.debut"></b-input> </label>
                            <label>Au:<b-input type="date" v-model="gardeNouvelle.fin"></b-input> </label>
                        </div>
                    </b-col>
                    <b-col></b-col>
                </b-row>
                <b-row id="ecart2" align-h="center" class="font-weight-bold">
                    <b-col cols="6"><b-button block pill variant="success" @click="gardeAjouter()">Ajouter</b-button></b-col>
                    <b-col cols="6"><b-button block pill variant="info" @click="gardeCancel()">Annuler</b-button></b-col>
                </b-row>
                <hr>
                <b-row id="ecart3" align-h="center" class="font-weight-bold">
                    <h6>Liste des gardes:</h6>
                </b-row>
                <b-row id="ecart2" align-h="center">
                    
                        <div v-for="(garde,index) of gardes" v-bind:key="index">
                            <h5 class="text-left">{{"garde "+(index + 1) +" : "}}
                                <b-badge pill variant="danger" @click="gardeSupprimer(index)">Supprimer</b-badge>
                            </h5> 
                            <label>Du: {{ garde.debut +" - "}}</label>
                            <label>Au: {{ garde.fin}}</label>
                            <hr>
                        </div>
                    
                </b-row>                                             
            </b-container>  
    </div>
  
</template>

<script>
export default {
    name:'GardeDetail',

    data(){
        return {
            gardes: this.$store.getters.getDetailGarde,
            nombreGarde:this.$store.getters.getProfilConnecte.nombreGarde,
            gardeNouvelle:{debut:'',fin:''}
        }
    },

    methods: {
        gardeAjouter(){
            if(this.gardeNouvelle.debut && this.gardeNouvelle.fin){
                var gd={};
                gd.debut=this.gardeNouvelle.debut;
                gd.fin= this.gardeNouvelle.fin;
                this.$store.commit('setDetailGarde',gd);
            }
        },
        gardeSupprimer(index){
            this.gardes.splice(index, 1);
            // this.$store.commit('setDetailGarde',this.gardeNouvelle);
        },
        gardeCancel(){
            this.$store.commit('back');
        },
        gardeNombre(){
                var nb=0;
                this.gardes.forEach(() => {nb=nb+1;});
               return nb;
        },
    }
}
</script>

<style scoped lang="scss">
    #ecart2 {
        margin-top: 2%;
    }
    #ecart3 {
        margin: 20px;
    }
    #ecart {
        margin-top: 5%;
    }
</style>